@import url('https://fonts.googleapis.com/css2?family=Mulish&display=swap');



code {
  font-family: Mulish, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Mulish", sans-serif;
}

.navbar {
  background-color: #fff !important;
}

.navbar-brand {
  font-size: 1.8rem;
  color: #9cc700 !important;
}

.navbar a {
  font-size: 1.2rem;
  text-transform: capitalize;
}

.menu_active {
  font-weight: bold;
  border-bottom: 1px solid #561085;
}

navbar a:hover {
  color: #3500c7 !important;
}

.form {
  width: 380px !important;
  padding: 20px;
}

@media (max-width: 991px) {
  .navbar-brand {
    font-size: 1.4rem !important;
  }

  .menu_active {
    font-weight: bold;
    border-bottom: none;
  }
}

/* HOME      PAGE */

#header {
  width: 100%;
  height: 80vh;
  margin-top: 70px;
}

#header h1 {
  margin: 0 0 10px 0;
  font-size: 48px;
  font-weight: 700;
  color: #000;
}

#header .brand-name {
  color: #3e9edd;
}

#header h2 {
  color: #575555;
  font-size: 22px;
}

#header .btn-get-started {
  font-weight: 500;
  font-size: 17px;
  letter-spacing: 1px;
  display: inline-block;
  padding: 10px 30px;
  border-radius: 50px;
  transition: 0.5s;
  color: #3e9edd ;
  border: 1px solid #3e9edd;
  text-decoration: none;
}

#header .btn-get-started:hover {
  background-color: #3e9edd;
  color: #fff;
}

.header-img {
  text-align: right;
}

#header .animate {
  animation: up-down 2s ease-in-out infinite alternate-reverse both;
}

@-webkit-keyframes up-down {
  0% {
    transform: translateY(10px);
  }
  100% {
    transform: translateY(-10px);
  }
}

@keyframes up-down {
  0% {
    transform: translateY(10px);
  }
  100% {
    transform: translateY(-10px);
  }
}